@font-face {
  font-family: "opensans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "opensans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  font-family: "opensans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  font-style: normal;
  color: #444444;
  background-color: #f1f1f1;
}

main {
  display: block;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: contain;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  margin: 0;
}

.container {
  width: 320px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1150px;
    padding: 0 50px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.title {
  margin: 0;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}

.interactive {
  display: inline-block;
  padding-top: 14px;
  padding-bottom: 14px;
  border: 2px solid #444444;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: #444444;
  text-decoration: none;
  cursor: pointer;
  text-align: center;
  background-color: transparent;
  text-transform: uppercase;
}
.interactive:focus {
  color: #ffffff;
  background-color: #444444;
  outline: none;
}
.interactive:hover {
  color: #ffffff;
  background-color: #444444;
}
.interactive:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}

.page__body {
  margin: 0;
  padding: 0;
  min-width: 320px;
}

.page-header {
  box-shadow: 0 1px 0 0 #ececec inset, 0 -1px 0 0 #ececec inset;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-header {
    box-shadow: none;
    background-color: #fafafa;
  }
}
@media (min-width: 768px) {
  .page-header--catalog, .page-header--order {
    background-color: transparent;
  }
}

.page-header__bar {
  background-color: #ffffff;
  min-height: 79px;
}
@media (min-width: 768px) {
  .page-header__bar {
    min-height: 88px;
    box-shadow: 0 -1px 0 0 #ececec inset;
  }
}

.page-header .container {
  padding-top: 22px;
}
@media (min-width: 768px) {
  .page-header .container {
    width: 768px;
    margin: 0 auto;
    padding-left: 50px;
    padding-right: 50px;
  }
}
@media (min-width: 1150px) {
  .page-header .container {
    width: auto;
    padding: 0;
    padding-top: 9px;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
  }
}

.page-header__logo {
  display: block;
  position: relative;
  z-index: 3;
  width: 86px;
  height: 35px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-header__logo {
    width: 113px;
    height: 46px;
    margin-left: 0;
    margin-right: auto;
  }
}
@media (min-width: 1150px) {
  .page-header__logo {
    width: 149px;
    height: 72px;
  }
}
.page-header__logo[href]:focus {
  opacity: 0.6;
}
.page-header__logo[href]:hover {
  opacity: 0.6;
}
.page-header__logo[href]:active {
  opacity: 0.3;
}

.page-header__img {
  width: 86px;
  height: 35px;
}
@media (min-width: 768px) {
  .page-header__img {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .page-header__img {
    width: 149px;
    height: 72px;
  }
}

.navigation {
  min-height: 79px;
  margin-top: -79px;
}
.navigation--no-js {
  margin-top: 0;
}
@media (min-width: 768px) {
  .navigation {
    margin-top: -88px;
    min-height: 88px;
  }
}

.navigation__wrapper {
  display: none;
  flex-direction: column;
  position: absolute;
  top: 79px;
  left: 0;
  right: 0;
  z-index: 2;
  background-color: #ffffff;
}
.navigation__wrapper--no-js {
  display: flex;
  position: static;
}
.navigation__wrapper--open {
  display: flex;
}
.navigation__wrapper--open-no-js {
  display: flex;
  position: static;
}
@media (min-width: 768px) {
  .navigation__wrapper {
    display: flex;
    position: static;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: transparent;
    width: 768px;
    padding-left: 50px;
    padding-right: 50px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 1150px) {
  .navigation__wrapper {
    width: 1150px;
  }
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .navigation__list--site {
    width: 334px;
    order: 2;
    display: flex;
    padding-top: 22px;
  }
}
@media (min-width: 1150px) {
  .navigation__list--site {
    order: 1;
    width: 405px;
    margin-right: 295px;
    padding-top: 0;
  }
}
@media (min-width: 768px) {
  .navigation__list--user {
    width: 347px;
    margin-left: auto;
    order: 1;
    display: flex;
  }
}
@media (min-width: 1150px) {
  .navigation__list--user {
    order: 2;
    width: 349px;
  }
}

.navigation__note {
  display: none;
}
@media (min-width: 768px) {
  .navigation__note {
    display: block;
    width: 259px;
    margin-left: auto;
    order: 3;
    font-size: 15px;
    text-align: right;
    padding-top: 21px;
    padding-right: 1px;
  }
}

.navigation__toggle {
  padding: 0;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 32px;
  right: 31px;
  background-color: transparent;
  z-index: 2;
}
.navigation__toggle--no-js {
  display: none;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation__toggle:focus {
  outline: none;
}
.navigation__toggle:focus .navigation__toggle-icon {
  color: #63d1bb;
}
.navigation__toggle:hover .navigation__toggle-icon {
  color: #63d1bb;
}
.navigation__toggle:active .navigation__toggle-icon {
  color: rgba(99, 209, 187, 0.3);
}

.navigation__toggle-icon--open {
  width: 20px;
  height: 14px;
  position: absolute;
  top: 0;
  right: 0;
}
.navigation__toggle-icon--close {
  width: 21px;
  height: 20px;
  position: absolute;
  top: -3px;
  right: -1px;
}
.navigation__toggle-icon--hide {
  display: none;
}

.navigation__link {
  font-weight: 700;
  text-decoration: none;
  color: #000000;
}
.navigation__link svg {
  fill: rgba(0, 0, 0, 0.2);
}
.navigation__link[href]:focus {
  outline: none;
  color: rgba(0, 0, 0, 0.6);
}
.navigation__link[href]:focus svg {
  fill: #63d1bb;
}
.navigation__link[href]:hover {
  color: rgba(0, 0, 0, 0.6);
}
.navigation__link[href]:hover svg {
  fill: #63d1bb;
}
.navigation__link[href]:active {
  color: rgba(0, 0, 0, 0.3);
}
.navigation__link[href]:active svg {
  fill: rgba(99, 209, 187, 0.3);
}
@media (min-width: 1150px) {
  .navigation__link--new {
    padding-right: 35px;
  }
}
@media (min-width: 1150px) {
  .navigation__link--sale {
    padding-left: 27px;
  }
}

.navigation__inner {
  display: none;
}
@media (min-width: 1150px) {
  .navigation__inner {
    order: 3;
    width: 405px;
    margin-right: auto;
    display: flex;
    padding-top: 21px;
  }
}

@media (min-width: 768px) {
  .site-list__item {
    width: 50%;
  }
}
@media (min-width: 1150px) {
  .site-list__item {
    width: auto;
  }
}

.site-list__link {
  font-size: 17px;
  min-height: 79px;
  display: flex;
  align-items: center;
  padding-left: 76px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 10px;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media (min-width: 768px) {
  .site-list__link {
    min-height: auto;
    font-size: 15px;
    padding-left: 0;
    padding-top: 0;
    box-shadow: none;
  }
}
@media (min-width: 1150px) {
  .site-list__link {
    min-height: 88px;
    font-size: 17px;
    padding: 0;
    border-right: 1px solid #ececec;
  }
}
@media (min-width: 1150px) {
  .site-list__link--catalog {
    padding-right: 35px;
  }
}
@media (min-width: 1150px) {
  .site-list__link--form {
    padding-left: 35px;
    border-right: none;
  }
}

.user-list__link {
  min-height: 79px;
  position: relative;
  font-size: 17px;
  display: flex;
  align-items: center;
  padding-left: 76px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 10px;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
.user-list__link--search svg {
  position: absolute;
  top: 32px;
  left: 32px;
}
@media (min-width: 768px) {
  .user-list__link--search svg {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (min-width: 768px) {
  .user-list__link--search .user-list__link-text {
    display: none;
  }
}
@media (min-width: 768px) {
  .user-list__link--search {
    padding: 0;
    width: 82px;
    border-left: 1px solid #ececec;
  }
}
.user-list__link--cart svg {
  position: absolute;
  top: 31px;
  left: 30px;
}
@media (min-width: 768px) {
  .user-list__link--cart svg {
    top: 50%;
    left: 29px;
    transform: translateY(-50%);
  }
}
@media (min-width: 768px) {
  .user-list__link--cart {
    padding-right: 0;
    padding-left: 82px;
    border-left: 1px solid #ececec;
  }
}
@media (min-width: 768px) {
  .user-list__link {
    min-height: 88px;
    box-shadow: none;
  }
}

.heading {
  background-color: #f1f1f1;
}
.heading--index {
  padding-top: 72px;
  min-height: 283px;
  background-image: url("../../img/hero-mobile@1x.jpg");
  background-color: #fafafa;
  background-size: auto;
  background-repeat: no-repeat;
  background-position: 50% -17px;
}
@media (min-resolution: 2dppx) {
  .heading--index {
    background-size: 320px auto;
    background-image: url("../../img/hero-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .heading--index {
    background-image: url("../../img/hero-tablet@1x.jpg");
    background-position: 50% -57px;
    padding-top: 98px;
    min-height: 571px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .heading--index {
    background-size: 768px auto;
    background-image: url("../../img/hero-tablet@2x.jpg");
  }
}
@media (min-width: 1150px) {
  .heading--index {
    background-position: 50% -45px;
    padding-top: 113px;
    min-height: 582px;
    background-image: url("../../img/hero-desktop@1x.jpg");
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .heading--index {
    background-size: 1150px auto;
    background-image: url("../../img/hero-desktop@2x.jpg");
  }
