    left: 0;
    right: 0;
    bottom: 0;
    transform: translateY(11px);
    z-index: 4;
    background-image: url("../../img/svg/zigzag-fill-tablet.svg");
  }
}
@media (min-width: 1150px) {
  .heading--catalog::after {
    height: 34px;
    background-image: url("../../img/svg/zigzag-fill-desktop.svg");
  }
}
@media (min-width: 768px) {
  .heading--catalog {
    padding-top: 43px;
    min-height: 170px;
  }
}
@media (min-width: 1150px) {
  .heading--catalog {
    padding-top: 57px;
    min-height: 216px;
  }
}
.heading--order {
  padding-top: 72px;
  min-height: 177px;
  position: relative;
}
.heading--order::after {
  display: none;
}
@media (min-width: 768px) {
  .heading--order::after {
    display: block;
    position: absolute;
    content: "";
    width: 100%;
    height: 24px;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translateY(11px);
    z-index: 4;
    background-image: url("../../img/svg/zigzag-fill-tablet.svg");
  }
}
@media (min-width: 1150px) {
  .heading--order::after {
    height: 34px;
    background-image: url("../../img/svg/zigzag-fill-desktop.svg");
