}

.heading__title {
  width: 260px;
  margin: 0 auto;
  padding-bottom: 20px;
  font-size: 27px;
  line-height: 30px;
  text-align: center;
  color: #000000;
}
@media (min-width: 768px) {
  .heading__title {
    width: 668px;
    margin-left: auto;
    margin-right: auto;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .heading__title {
    font-size: 53px;
    line-height: 57px;
  }
}

.popular {
  position: relative;
  background-color: #fafafa;
}
.popular::after {
  display: none;
}
@media (min-width: 768px) {
  .popular::after {
    display: block;
    position: absolute;
    content: "";
    width: 100%;
    height: 24px;
    left: 0;
    right: 0;
    bottom: 1px;
    z-index: 4;
    background-image: url("../../img/svg/zigzag-fill-tablet.svg");
  }
}
@media (min-width: 1150px) {
  .popular::after {
    height: 34px;
    background-image: url("../../img/svg/zigzag-fill-desktop.svg");
  }
}
@media (min-width: 768px) {
  .popular {
    padding-bottom: 12px;
  }
}
@media (min-width: 1150px) {
  .popular {
    padding-bottom: 16px;
  }
}

.popular .container {
  width: 100%;
  padding: 0;
}
@media (min-width: 768px) {
  .popular .container {
    width: 768px;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .popular .container {
    width: 1150px;
    padding: 0 50px;
  }
