@media (min-width: 768px) {
  .popular__link-text {
    font-size: 20px;
    line-height: 30px;
  }
}

.action {
  padding-top: 32px;
  padding-bottom: 41px;
  position: relative;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .action {
    padding-top: 5px;
    padding-bottom: 44px;
  }
}
@media (min-width: 1150px) {
  .action {
    padding-top: 2px;
    padding-bottom: 98px;
  }
}
.action::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 6px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 4;
  background-image: url("../../img/svg/zigzag-line-mobile.svg");
}
@media (min-width: 768px) {
  .action::after {
    height: 17px;
    background-image: url("../../img/svg/zigzag-line-tablet.svg");
  }
}
@media (min-width: 1150px) {
  .action::after {
    width: auto;
    left: 50px;
    right: 50px;
    height: 23px;
    background-image: url("../../img/svg/zigzag-line-desktop.svg");
  }
}
.action .container {
  position: relative;
}
@media (min-width: 768px) {
  .action .container {
    display: grid;
    grid-template-columns: 292px auto;
    grid-column-gap: 42px;
  }
}
@media (min-width: 1150px) {
  .action .container {
    grid-template-columns: 467px 1fr;
    grid-template-rows: repeat(3, auto);
    grid-column-gap: 58px;
