  }
}

.action__header {
  margin-bottom: 13px;
}
@media (min-width: 768px) {
  .action__header {
    display: flex;
    justify-content: space-between;
    grid-area: 1/1/2/3;
    padding-top: 34px;
    margin-bottom: 0;
  }
}
@media (min-width: 1150px) {
  .action__header {
    grid-area: 1/1/2/3;
    justify-content: start;
    padding-top: 114px;
    padding-left: 30px;
    z-index: 2;
  }
}

@media (min-width: 768px) {
  .action__description {
    grid-area: 2/1/3/2;
    padding-top: 39px;
  }
}
@media (min-width: 1150px) {
  .action__description {
    padding-top: 25px;
    padding-left: 174px;
    grid-area: 2/1/3/2;
  }
}

.action__title {
  margin-bottom: 3px;
}
@media (min-width: 768px) {
  .action__title {
    order: 1;
    margin-bottom: 0;
  }
}
@media (min-width: 1150px) {
  .action__title {
    order: 2;
  }
}

.action__subtitle {
  display: inline-block;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;
